<template>
  <div class="no-select">

    <div class="p-3 d-flex align-items-center flex-wrap">


      <div class="m-1 bg-light rounded p-1">
        <span class="fa fa-tags"></span>
        <b class="mx-1">برند:</b>
        <span>دیرین دیرین</span>
      </div>

      <div class="m-1 bg-light rounded p-1">
        <span class="fa fa-hourglass-half"></span>
        <b class="mx-1">مدت زمان ویدئو:</b>
        <span>{{ videoDuration }}</span>
      </div>

      <div class="m-1 ml-auto bg-success text-white rounded p-1">
        <b class="mr-1">امتیاز ویدئو:</b>
        <span>342</span>
      </div>


    </div>

    <div id="video-player">
      <video class="img-fill" ref="videoElement">
        <source label="fullHD" src="../assets/videos/video.mp4" type="video/mp4">
        <source label="720p" src="../assets/videos/video.mp4" type="video/mp4">
        <source label="360p" src="../assets/videos/video.mp4" type="video/mp4">
      </video>

      <div id="on-video-controller" v-if="videoMode === 'show-controller'"
           class="d-flex justify-content-center align-items-center text-center text-white">
        <span class="fa fa-play fa-4x cursor-pointer" @click="playVideo"></span>
      </div>
      <div id="on-video-container" v-if="videoMode === 'show-confirm-form'"
           class="d-flex flex-column justify-content-center align-items-center">

        <div class="text-justify text-last-center" style="max-width: 350px">
          <h4>برای دریافت امتیاز ویدئو کد را وارد کنید</h4>
          <img src="../assets/images/random-code.png" width="135px" class="my-2 bg-secondary p-2 rounded"/>

          <input type="tel" class="text-center form-control" v-bind="userConfirmEntered"/>
          <div class="p-2">
            <button class="btn btn-success" @click="resumeVideo(true)">تایید و ادامه</button>
            <button class="btn btn-warning ml-1"
                    data-toggle="modal" data-target="#confirm-to-resume-without-point">
              انصراف و ادامه
            </button>
          </div>
        </div>

      </div>
    </div>

    <div class="my-3 text-justify ">
      <h4>
        <span>توضیحات:</span>
      </h4>
      <p>
        به نظر شما دولت در آینده برای درآمدزایی بیشتر چه کارهایی می‌کنه؟ الف) ما رو گرو میذاره وام می‌گیره. ب) ماهانه از
        مردم یارانه می‌گیره. ج) ما رو صادر می‌کنه د) مجبورمون می کنه سر چهارراه‌ها و تو مترو نفت خام بفروشیم.
      </p>
    </div>

    <div class="my-3 d-flex flex-wrap align-items-center text-white small">

      <div class="m-1 p-1 bg-info rounded">
        <span class="fa fa-hashtag mr-1"></span>
        <span>طنز</span>
      </div>

      <div class="m-1 p-1 bg-info rounded">
        <span class="fa fa-hashtag mr-1"></span>
        <span>دیرین دیرین</span>
      </div>

      <div class="m-1 p-1 bg-info rounded">
        <span class="fa fa-hashtag mr-1"></span>
        <span>سیاسی</span>
      </div>

      <div class="m-1 p-1 bg-info rounded">
        <span class="fa fa-hashtag mr-1"></span>
        <span>اقتصاد</span>
      </div>

    </div>

    <div class="my-3 p-3">

    </div>

    <div class="modal fade" id="confirm-to-resume-without-point" tabindex="-1" role="dialog"
         aria-labelledby="confirm-to-resume-without-pointLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="confirm-to-resume-without-pointLabel">هشدار</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            در صورت تایید هیچ امتیازی برای شما در نظر گرفته نخواهد شد
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-info" data-dismiss="modal">انصراف</button>
            <button type="button" class="btn btn-danger" data-dismiss="modal"
                    @click="resumeVideo(false)">
              تایید و ادامه ویدئو
            </button>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
import {random} from "@/assets/tools/Functions";

export default {
  name: "ViewingVideos",
  data: () => ({
    videoMode: 'show-controller',
    confirmTime: null,
    userConfirmEntered: null,
    videoDuration: 0
  }),
  methods: {
    playVideo: function () {
      this.videoMode = 'playing-video';
      this.$refs.videoElement.play();
      const videoTotalTime = this.$refs.videoElement.duration;
      this.videoDuration = Math.floor(videoTotalTime / 60) + ':' + Math.ceil(videoTotalTime % 60);
      this.confirmTime = Math.round(random(videoTotalTime - 20, videoTotalTime));
    },
    resumeVideo: function (confirm) {
      if (confirm) {
        if (this.userConfirmEntered !== '4735440') {
          alert('کد وارد شده صحیح نیست');
          return false;
        }
      }

      this.confirmTime = null;
      this.videoMode = 'playing-video';
      this.$refs.videoElement.play();

    }
  },
  mounted: function () {
    window.onblur = () => {
      if (this.videoMode === 'playing-video') {
        this.$refs.videoElement.pause();
        this.videoMode = 'show-controller';
      }
    }
    this.$refs.videoElement.onratechange = () => {
      this.$refs.videoElement.playbackRate = 1;
    }
    this.$refs.videoElement.ontimeupdate = () => {
      const currentTime = this.$refs.videoElement.currentTime;
      if (this.confirmTime && this.confirmTime <= currentTime) {
        this.$refs.videoElement.pause();

        //if in full-screen mode close it first
        if (document.fullscreenElement) {
          document.exitFullscreen().catch(() => {
          });
        }

        this.videoMode = 'show-confirm-form';
      }

    }
    this.$refs.videoElement.addEventListener('loadedmetadata', () => {
      const videoTotalTime = this.$refs.videoElement.duration;
      this.videoDuration = Math.floor(videoTotalTime / 60) + ':' + Math.ceil(videoTotalTime % 60);
    })

  }
}
</script>

<style scoped>
#video-player {
  position: relative;
  background: black;
}

#on-video-container, #on-video-controller {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  color: #00ffff;
  background: rgba(0, 0, 0, 0.89);
}
</style>